iframe {
  width: 100%;
  height: 100%;
}

.fa {
	position: relative;
	text-align: center;
	width: 10px !important;
	height: 10px;
}
.fa:before {
	position: absolute;
	border: 4px solid transparent;
	border-left-color: #8aa4af;
	content: "";
	width: 0;
	height: 0;
	left: 0;
	top: 0;
}
.sidebar-menu li:hover > a > .fa:before {
	border-left-color: #fff;
}
.sidebar-menu li.active > a > .fa:before {
	border-top-color: #fff;
	border-left-color: #2c3b41;
	top: 3px;
}

.treeview-menu {
	display: none !important;
	padding-left: 10px !important;
}
.sidebar-menu li.active > .treeview-menu,
.sidebar-menu li.active + .treeview-menu {
	display: block !important;
}
